import React, { Component } from 'react';
import { BrowserRouter as Router, Redirect, Switch, Route, Link} from 'react-router-dom';
import Menu from './components/menu/menu';
import Users from './components/users/users';
import Login from './components/login/login';
import './components/all_style.css';
import showDate from './components/date'


class App extends Component {

constructor(props){
  super(props);
  this.token = this.token.bind(this);
  this.end = this.end.bind(this);
  this.state =  {
      name:'admin',
      pass:'123',
      token:'1'
  }

}


sessionRec = (check) => {
//тут надо сделать запрос к серверу на проверку пользователя
//и если такой есть
//то вернуть значения (имя токен) и записать его в сосояние
  if(check.name === this.state.name & check.pass === this.state.pass){
    let rec = JSON.stringify({token:this.state.token, name:this.state.name, date:Date()});
    sessionStorage.setItem('TG-Session', rec); //Создание сессии
    this.setState({session:true});
    document.getElementById('first_name').value = null;
    document.getElementById('password').value = null;
  }else{
    document.getElementById('first_name').value = null;
    document.getElementById('password').value = null;
  }

}

end(){
  sessionStorage.removeItem('TG-Session');
  this.setState({session:false});
}

token(){
   if(!sessionStorage.length == 0){
     var storSession = JSON.parse(sessionStorage.getItem('TG-Session'));
     return storSession.token == this.state.token;
   }else{
     return false;
   }

 }



checkLogIn(log){

  if(log){
    console.log('Вошел');

    return(
      <div>

      <Switch>
      <Redirect from = "/login/" to="/"/>
      </Switch>

      <Route path="/users/" component={Users} />

      </div>
    );

  }else{
    console.log('Не Вошел');

    var login = () =>{
      return(<Login sessionRec={this.sessionRec} />);
    }

        return(
          <div>
          <Switch>
          <Redirect from = "/users/" to = "/login/"/>
          </Switch>

          <Route path = "/login/" component={login} />

          </div>
        );

  }
}


  render() {

var menu = () => {
    return <Menu end={this.end} token={this.token}/>
};


    return (



    <Router>

    <Route path="/" component={menu} />

    {this.checkLogIn(this.token())}

    </Router>


    );
  }
}

export default App;
