import React, { Component } from 'react';
import './users.css';



class UserCard extends Component {
constructor(props){
  super(props)
  this.state = {
    isOpen:false
  }
}

render(){

  var Del =(
    <p
    className="p_title hover_red"
    style={{cursor:'pointer'}}
    onClick={() => {this.setState({ isOpen:true})}}
    >Удалить</p>
  );

  var Exactly =(
    <p className='p_title' >
    <span
    style={{color:'#b71c1c', cursor:'pointer'}}
    onClick={() => {this.props.userDelet(this.props.num)}}
    >Да</span>
    &nbsp;|&nbsp;
    <span
    style={{color:'#00796b', cursor:'pointer'}}
    onClick={() => {this.setState({ isOpen:false})}}
    >Нет</span>
    </p>
  );




  return(


      <div className="card_user card-panel ">

      <table width="100%" border="1" cellSpacing="0" cellPadding="0">
      <tbody>
        <tr>
          <td><p className="p_title">Имя</p></td>
          <td>

          <p>{this.props.name}</p>

          </td>
          <td rowSpan="3">

          <p>{this.props.comment}</p>

          </td>
          <td className="r p_title"><p></p></td>
        </tr>
        <tr>
          <td><p className="p_title">Пароль</p></td>
          <td><p>{this.props.pass}</p></td>
          <td>&nbsp;</td>
        </tr>
        <tr>
          <td><p className="p_title">Дата входа</p></td>
          <td><p>{this.props.date}</p></td>
          <td className="r">
          {!this.state.isOpen? Del : Exactly}
          </td>
        </tr>
      </tbody>
    </table>

      </div>

    );
  }
}


export default UserCard;
