import React, { Component } from 'react';
import './users.css';




class AddUser extends Component {

  render(){


    return(

      <div>
      <div className="card_user card-panel ">

        <div className="">

        <div className="input-field add_user">
        <textarea id="name" className="materialize-textarea type-text"></textarea>
        <label className='alert' htmlFor="name"><p>Имя</p></label>
        </div>

        <div className="input-field add_user">
        <textarea id="password" className="materialize-textarea type-text"></textarea>
        <label className='alert' htmlFor="password"><p>Пароль</p></label>
        </div>

        <div className="input-field add_user">
        <textarea id="comments" className="materialize-textarea type-text"></textarea>
        <label htmlFor="comments"><p>Комментарий</p></label>
        </div>

        </div>
        <div style={{marginTop:'20px'}}>

        <button
        type="button"
        className="waves-effect waves-light btn"
        onClick={() => {this.props.userListState({
          name: document.getElementById('name').value,
          pass: document.getElementById('password').value,
          comment: document.getElementById('comments').value
        });}}
        >
        <p>Добавить</p>
        </button>

        </div>

      </div>
      <hr/>
      </div>

    );
  }
}

export default AddUser;
