import React, { Component } from 'react';
import { BrowserRouter as Router, Redirect, Switch, Route, Link} from 'react-router-dom';
import './menu.css';



class Menu extends Component {




  render() {


    var itemIn = (

      <div>
      <li ><Link to='/login/'>Войти</Link></li>
      </div>

    );

    var itemOut = (

      <div>
      <li><Link to='/users/'>Пользователи</Link></li>
      <li><Link to='/login/' onClick={()=>{this.props.end()}}>Выйти</Link></li>
      </div>

    );



    return (

      <div className='body_menu grey lighten-2 row'>

        <nav className='body_menu grey lighten-2 container'>
            <ul className="right">

            {this.props.token() ? itemOut : itemIn}

            </ul>

            <ul className="left">
              <li className='font_grey'><Link to='/'>TG-parts</Link></li>
            </ul>

        </nav>

    </div>

    );
  }
}

export default Menu;
